let timeoutBox: any[] = []
let intervalBox: any[] = []
/**
 * 
 * @param {object} r router
 */
export const init = function (r) {
	if (r && r.beforeEach) {
		r.beforeEach(() => {
			timeoutBox.map((e) => {
				window.clearTimeout(e)
			})
			intervalBox.map((e) => {
				window.clearInterval(e)
			})
			timeoutBox.length = 0
			intervalBox.length = 0
		})
	} else {
		console.warn(
			'plug-r-qwrouterrouterVue.use(plugRQw,{router,...})'
		)
	}
}

export const setTimeout = function (fn, time) {
	let handler = window.setTimeout(fn, time)
	timeoutBox.push(handler)
	return handler
}

export const setInterval = function (fn, time) {
	let handler = window.setInterval(fn, time)
	intervalBox.push(handler)
	return handler
}

export default {
	init,
	setTimeout,
	setInterval
}
