import axios from 'axios'
import type { AxiosInstance, AxiosRequestConfig, AxiosResponse } from 'axios'
import _ from 'lodash'
import { t } from '../locale'

import { counts } from './spin'
import type { Collection } from '../public'

const host = window.location.origin

let proxy: any = null
const T = (...arg: Parameters<typeof t>) => t.apply(proxy, arg)

interface ServiceR extends AxiosInstance {
	store?: any
}

interface RequestConfigR extends AxiosRequestConfig {
	spin?: boolean
	noEmptyStr?: boolean
}

// 
let service: ServiceR = axios.create({
	baseURL: host,
	withCredentials: true // cookie
})

function notInitYet() {
	console.info('storestoreVue.use(plugRQw,{store:store})Pinia{store:useStore}')
}

/**
 * 
 */
service.interceptors.request.use(
	(q) => {
		return q
	},
	(e) => {
		return Promise.reject(e)
	}
)

function logoutHandle() {
	if (service.store) {
		if (typeof service.store === 'function') {
			const store = service.store()
			if (store.logout) {
				store.logout()
			}
		} else {
			service.store.dispatch('logout')
		}
	} else {
		notInitYet()
	}
}

/**
 * 
 */
service.interceptors.response.use(
	(r) => {
		if (proxy && r && r.data && (r.data.code === 403 || r.data.code === 409)) {
			proxy.messageBox({
				content: T('r.http.' + r.data.code),
				onOk: logoutHandle
			})
		}
		return r
	},
	(e) => {
		if (proxy && e && e.response && (e.response.status === 403 || e.response.status === 409)) {
			proxy.messageBox({
				content: T('r.http.' + e.response.status),
				onOk: logoutHandle
			})
		}
		console.warn('', e)
		return Promise.reject(e)
	}
)

/**
 * 
 */
function checkResult(r: Partial<AxiosResponse>, msg?: string, rPath?: string[], config?: RequestConfigR) {
	if (config && config.spin) {
		counts(false)
	}
	let y = true
	let d = r && r.data
	if (d) {
		rPath = rPath ? rPath : []
		for (let e of rPath) {
			d = d[e]
			y = y && d
		}
		if (y) {
			return d
		}
		msg && console.warn(msg)
		return false
	}
	msg && console.warn(msg)
	return false
}

function handleRequest(
	method: string,
	url: string,
	data?: Collection,
	msg?: string,
	rPath?: string[],
	config?: RequestConfigR,
	isUrlData?: boolean
) {
	return new Promise((s, j) => {
		switch (method) {
			case 'get':
				service
					.get(url, { params: data })
					.then((r) => {
						let d = checkResult(r, msg, rPath, config)
						if (d) {
							s(d)
						} else {
							j(r)
						}
					})
					.catch((e) => {
						checkResult({}, msg, rPath, config)
						j(e)
					})
				break
			case 'delete':
				let keyT = isUrlData ? 'params' : 'data'
				service
					.delete(url, { [keyT]: data })
					.then((r) => {
						let d = checkResult(r, msg, rPath, config)
						if (d) {
							s(d)
						} else {
							j(r)
						}
					})
					.catch((e) => {
						checkResult({}, msg, rPath, config)
						j(e)
					})
				break
			case 'post':
				service
					.post(url, data, config)
					.then((r) => {
						let d = checkResult(r, msg, rPath, config)
						if (d) {
							s(d)
						} else {
							j(r)
						}
					})
					.catch((e) => {
						checkResult({}, msg, rPath, config)
						j(e)
					})
				break
			case 'put':
				service
					.put(url, data, config)
					.then((r) => {
						let d = checkResult(r, msg, rPath, config)
						if (d) {
							s(d)
						} else {
							j(r)
						}
					})
					.catch((e) => {
						checkResult({}, msg, rPath, config)
						j(e)
					})
				break
			default:
		}
	})
}

/**
 * 
 * @param method 
 * @param url 
 * @param data 
 * @param msg 
 * @param rPath 
 * @param config 
 * @param isUrlData delete true:params,false:data
 * @returns {Promise<*>}
 */
function checkRequest(
	method: string,
	url: string,
	data: Collection = {},
	msg?: string,
	rPath?: string[],
	config?: RequestConfigR,
	isUrlData?: boolean
) {
	return new Promise((s, j) => {
		if (url) {
			if (config && config.spin) {
				counts(true)
			}
			let url_ = url
			if (window && window.hasOwnProperty('g')) {
				/*config.jspublicindex.html
         :
         config.js window.g={mgrURL:'http://mgr.myweb.com'}
          /mgr/file  'http://mgr.myweb.com/file'
         */

				// @ts-ignore
				const _g = window.g
				let httpEnv = Object.keys(_g)
					.filter((e) => e.indexOf('URL') > -1)
					.map((e) => e.replace('URL', ''))

				for (let e of httpEnv) {
					let regExp = new RegExp('^/' + e + '(?=/.*$)', 'i')
					if (regExp.test(url) && _g[e + 'URL']) {
						url_ = _g[e + 'URL'] + url.replace(regExp, '')
						break
					}
				}
			}
			let data_: Collection | undefined
			if (config && config.headers && config.headers['Content-Type'] === 'multipart/form-data') {
				data_ = data
			} else {
				if (data && !_.isEmpty(data)) {
					if (Array.isArray(data)) {
						data_ = []
						for (let e of data) {
							if (e || e === 0 || e === false || (e === '' && config && !config.noEmptyStr)) {
								data_.push(e)
							}
						}
					} else {
						data_ = {}
						for (let key in data) {
							if (
								data.hasOwnProperty(key) &&
								(data[key] ||
									data[key] === 0 ||
									data[key] === false ||
									(data[key] === '' && config && !config.noEmptyStr))
							) {
								data_[key] = data[key]
							}
						}
					}
				}
			}
			let method_ = method.toLowerCase()
			handleRequest(method_, url_, data_, msg, rPath, config, isUrlData)
				.then((r) => {
					s(r)
				})
				.catch((e) => {
					j(e)
				})
		} else {
			console.error(':url')
			j(':url')
		}
	})
}

/**
 * @description axiosthis.$fetchthis.$fetch.get("/getData",{id:1})
 * vue3setupproxy
 * Es Modules
 * @class
 */
export default {
	/**
	 * @description spin
	 * @function
	 * @param {object} store vuexstorepiniauseStore
	 * @param app vue
	 */
	init(store: any, app: any) {
		service.store = store
		proxy = app.config.globalProperties
	},
	/**
	 * post 
	 * @function
	 * @param {string} url 
	 * @param {object} data 
	 * @param {string} msg 
	 * @example this.$fetch.post("/getDataB",{name:'ricky'},"B")
	 * @param {Array.<string>} rPath [data,list]data.list,
	 * @param {object} config    spin:true
	 * @return {Promise<object>}
	 * @example this.$fetch.post("/getData",{id:1},null,['result','list'])
	 *  .then(r=>{
	 *      console.log(r)
	 *      r:data.result.list,data
	 *  })
	 *
	 * 
	 *  5(url)url,data,msg,rPath,config
	 *  this.$fetch.post("/setData",{},null,[],{
	 *     headers: {
	 *         'Content-Type': 'multipart/form-data'
	 *       },
	 *       spin:true
	 *   }
	 *  )
	 */
	post(url: string, data?: Collection, msg?: string, rPath?: string[], config?: RequestConfigR) {
		return new Promise((s, j) => {
			checkRequest('post', url, data, msg, rPath, config)
				.then((r) => {
					s(r)
				})
				.catch((e) => {
					j(e)
				})
		})
	},
	/**
	 * put
	 * @param {string} url 
	 * @param {object} data 
	 * @param {string} msg 
	 * @param {Array.<string>} rPath 
	 * @param {object} config    spin:true
	 * @return {Promise<unknown>}
	 */
	put(url: string, data?: Collection, msg?: string, rPath?: string[], config?: RequestConfigR) {
		return new Promise((s, j) => {
			checkRequest('put', url, data, msg, rPath, config)
				.then((r) => {
					s(r)
				})
				.catch((e) => {
					j(e)
				})
		})
	},
	/**
	 * get
	 * @param {string} url 
	 * @param {object} data 
	 * @param {string} msg 
	 * @param {Array.<string>} rPath 
	 * @param {object} config    spin:true
	 * @return {Promise<unknown>}
	 * PS: get(delete)urldatadatadata
	 * '/devices',id=2,name=meter
	 *  urlurl = '/devices?id=2&name=meter'
	 *  urldata,url = '/devices',data={id:2,name:'meter'}
	 */
	get(url: string, data?: Collection, msg?: string, rPath?: string[], config?: RequestConfigR) {
		return new Promise((s, j) => {
			checkRequest('get', url, data, msg, rPath, config)
				.then((r) => {
					s(r)
				})
				.catch((e) => {
					j(e)
				})
		})
	},
	/**
	 * delete 
	 * @param {string} url 
	 * @param {object} data 
	 * @param {string} msg 
	 * @param {Array.<string>} rPath 
	 * @param {object} config    spin:true
	 * @param isUrlData  true:params,false:data
	 * @return {Promise<unknown>}
	 */
	delete(
		url: string,
		data?: Collection,
		msg?: string,
		rPath?: string[],
		config?: RequestConfigR,
		isUrlData: boolean = true
	) {
		return new Promise((s, j) => {
			checkRequest('delete', url, data, msg, rPath, config, isUrlData)
				.then((r) => {
					s(r)
				})
				.catch((e) => {
					j(e)
				})
		})
	},
	/**
	 *    
	 * @example this.$fetch.all(
	 *  [
	 *    this.$fetch.get("/getData"),
	 *    this.$fetch.post("/getDataB",{name:'ricky'})
	 *  ]
	 * )
	 */
	all: axios.all,
	/**
	 *  
	 * @example this.$fetch.all(
	 *  [
	 *    this.$fetch.get("/getData"),
	 *    this.$fetch.post("/getDataB",{name:'ricky'})
	 *  ]
	 * )
	 *  .then(
	 *    this.$fetch.spread((result1,result2)=>{
	 *        console.log(result1,result2)
	 *    })
	 *   )
	 */
	spread: axios.spread,
	/**
	 * axios.createaxios
	 */
	config: service
}
